import { Component, Prop, State, Event, EventEmitter, Element, Watch, h, Host } from '@stencil/core';
import { AutocompleteMenuItem } from '../bh-autocomplete-menu/bh-autocomplete-menu';

export type InputAutocompleteState = 'placeholder' | 'filled' | 'hover' | 'focused';

@Component({
  tag: 'bh-input-autocomplete',
  styleUrl: 'bh-input-autocomplete.css',
  shadow: true,
})
export class BhInputAutocomplete {
  @Element() el!: HTMLElement;

  // Base input props
  /**
   * Input state
   */
  @Prop() state?: InputAutocompleteState;

  /**
   * Whether input has error
   */
  @Prop() error: boolean = false;

  /**
   * Whether input is required
   */
  @Prop() required: boolean = false;

  /**
   * Whether input is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether to show help icon
   */
  @Prop() showHelpIcon: boolean = false;

  /**
   * Whether to show leading icon
   */
  @Prop() showLeadingIcon: boolean = true;

  /**
   * Whether to show label
   */
  @Prop() showLabel: boolean = true;

  /**
   * Whether to show hint text
   */
  @Prop() showHintText: boolean = true;

  /**
   * Label text
   */
  @Prop() label: string = 'Search';

  /**
   * Placeholder text
   */
  @Prop() placeholder: string = 'Search...';

  /**
   * Hint text
   */
  @Prop() hintText: string = 'This is a hint text to help user.';

  /**
   * Leading icon name
   */
  @Prop() leadingIcon: string = 'search';

  /**
   * Help icon tooltip
   */
  @Prop() helpIconTooltip: string = 'Help';

  /**
   * Current value
   */
  @Prop({ mutable: true }) value: string = '';

  /**
   * Width of the component
   */
  @Prop() width: string = '450px';

  // Autocomplete-specific props
  /**
   * Available options
   */
  @Prop() options: AutocompleteMenuItem[] = [];

  /**
   * Maximum number of options to show
   */
  @Prop() maxOptions: number = 10;

  /**
   * Enable multi-select mode
   */
  @Prop() multiSelect: boolean = false;

  /**
   * Minimum search length to show options
   */
  @Prop() minSearchLength: number = 3;

  /**
   * Selected items (for multi-select)
   */
  @Prop({ mutable: true }) selectedItems: AutocompleteMenuItem[] = [];

  @State() showDropdown: boolean = false;
  @State() focusedOptionIndex: number = -1;
  @State() isFocused: boolean = false;
  @State() isHovered: boolean = false;
  @State() isTagClickOpen: boolean = false;

  /**
   * Event emitted when value changes
   */
  @Event() bhChange!: EventEmitter<string>;

  /**
   * Event emitted when selected items change
   */
  @Event() bhSelectedItemsChange!: EventEmitter<AutocompleteMenuItem[]>;

  /**
   * Event emitted when input is focused
   */
  @Event() bhFocus!: EventEmitter<void>;

  /**
   * Event emitted when input is blurred
   */
  @Event() bhBlur!: EventEmitter<void>;

  /**
   * Event emitted when help icon is clicked
   */
  @Event() bhHelpClick!: EventEmitter<void>;

  /**
   * Event emitted when an option is selected
   */
  @Event() bhOptionSelect!: EventEmitter<AutocompleteMenuItem>;

  @Watch('value')
  handleValueChange(newValue: string) {
    if (newValue && newValue.length >= this.minSearchLength) {
      this.showDropdown = true;
    }
  }

  private get filteredOptions(): AutocompleteMenuItem[] {
    let baseOptions: AutocompleteMenuItem[] = [];

    if (this.isTagClickOpen && this.multiSelect) {
      baseOptions = this.selectedItems;
    } else if (this.showDropdown && (!this.value || this.value.length === 0)) {
      baseOptions = this.options.slice(0, this.maxOptions);
    } else if (this.value && this.value.length >= this.minSearchLength) {
      const searchLower = this.value.toLowerCase();
      const filtered = this.options.filter(option =>
        option.label.toLowerCase().includes(searchLower)
      );
      baseOptions = filtered.slice(0, this.maxOptions);
    }

    return baseOptions.map(option => ({
      ...option,
      selected: this.multiSelect && this.selectedItems.some(item => item.id === option.id),
    }));
  }

  private get shouldShowClearButton(): boolean {
    return !this.disabled && (
      (this.multiSelect && this.selectedItems.length > 0) ||
      (!this.multiSelect && Boolean(this.value && this.value.length > 0))
    );
  }

  private get visibleTags(): { tags: AutocompleteMenuItem[]; remaining: number } {
    if (!this.multiSelect || this.selectedItems.length === 0) {
      return { tags: [], remaining: 0 };
    }

    const containerWidth = parseFloat(this.width.replace('px', '')) || 450;
    const searchIconWidth = this.showLeadingIcon ? 40 : 0;
    const clearButtonWidth = this.shouldShowClearButton ? 32 : 0;
    const inputFieldMinWidth = 60;
    const padding = 24;
    const reservedWidth = searchIconWidth + clearButtonWidth + inputFieldMinWidth + padding;
    const availableWidth = Math.max(containerWidth - reservedWidth, 80);

    let currentWidth = 0;
    const visibleTagsList: AutocompleteMenuItem[] = [];
    const tagSpacing = 8;
    const counterWidth = 40;

    const reversedItems = [...this.selectedItems].reverse();

    for (let i = 0; i < reversedItems.length; i++) {
      const tag = reversedItems[i];
      const baseTagWidth = Math.min(tag.label.length * 8 + 36, 150);
      const remainingAfterThis = reversedItems.length - (i + 1);
      const needsCounter = remainingAfterThis > 0;
      const totalRequiredWidth = currentWidth + baseTagWidth + (needsCounter ? counterWidth : 0);

      if (totalRequiredWidth <= availableWidth) {
        visibleTagsList.push(tag);
        currentWidth += baseTagWidth + tagSpacing;
      } else {
        break;
      }
    }

    const finalVisibleTags = visibleTagsList.reverse();
    return {
      tags: finalVisibleTags,
      remaining: this.selectedItems.length - finalVisibleTags.length,
    };
  }

  private handleInputChange = (e: Event) => {
    const target = e.target as HTMLInputElement;
    this.value = target.value;
    this.bhChange.emit(this.value);
    this.isTagClickOpen = false;

    if (this.value.length >= this.minSearchLength) {
      this.focusedOptionIndex = -1;
    } else {
      this.showDropdown = false;
    }
  };

  private handleInputFocus = () => {
    this.isFocused = true;
    this.bhFocus.emit();

    if (this.value && this.value.length >= this.minSearchLength && this.filteredOptions.length > 0) {
      this.showDropdown = true;
    }
  };

  private getInputTextState(): 'default' | 'hover' | 'focus' | 'filled' | 'disabled' | 'error' | undefined {
    if (this.state === 'placeholder') {
      return 'default';
    }
    if (this.state === 'focused') {
      return 'focus';
    }
    return this.state as 'default' | 'hover' | 'focus' | 'filled' | 'disabled' | 'error' | undefined;
  }

  private handleInputBlur = () => {
    setTimeout(() => {
      this.isFocused = false;
      this.showDropdown = false;
      this.focusedOptionIndex = -1;
      this.isTagClickOpen = false;
      this.bhBlur.emit();
    }, 200);
  };

  private handleOptionSelect = (event: CustomEvent<AutocompleteMenuItem>) => {
    const option = event.detail;
    if (option.disabled) return;

    this.bhOptionSelect.emit(option);

    if (this.multiSelect) {
      const isSelected = this.selectedItems.some(item => item.id === option.id);
      const newItems = isSelected
        ? this.selectedItems.filter(item => item.id !== option.id)
        : [...this.selectedItems, option];

      this.selectedItems = newItems;
      this.bhSelectedItemsChange.emit(newItems);
      this.value = '';
      this.bhChange.emit('');
      this.isTagClickOpen = false;
      this.showDropdown = false;
    } else {
      this.value = option.label;
      this.bhChange.emit(option.label);
      this.showDropdown = false;
    }
  };

  private handleTagRemove = (tag: AutocompleteMenuItem) => {
    const newItems = this.selectedItems.filter(item => item.id !== tag.id);
    this.selectedItems = newItems;
    this.bhSelectedItemsChange.emit(newItems);
  };

  private handleTagClick = (e: MouseEvent) => {
    if ((e.target as HTMLElement).closest('.tag-dismiss')) {
      return;
    }

    e.preventDefault();
    e.stopPropagation();

    if (!this.disabled) {
      this.isTagClickOpen = true;
      this.showDropdown = true;
      this.isFocused = true;
    }
  };

  private handleClearAll = () => {
    if (this.multiSelect) {
      this.selectedItems = [];
      this.bhSelectedItemsChange.emit([]);
    }
    this.value = '';
    this.bhChange.emit('');
  };

  private handleKeyDown = (e: KeyboardEvent) => {
    if (!this.showDropdown || this.filteredOptions.length === 0) return;

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault();
        this.focusedOptionIndex = this.focusedOptionIndex < this.filteredOptions.length - 1
          ? this.focusedOptionIndex + 1
          : 0;
        break;
      case 'ArrowUp':
        e.preventDefault();
        this.focusedOptionIndex = this.focusedOptionIndex > 0
          ? this.focusedOptionIndex - 1
          : this.filteredOptions.length - 1;
        break;
      case 'Enter':
        e.preventDefault();
        if (this.focusedOptionIndex >= 0 && this.focusedOptionIndex < this.filteredOptions.length) {
          const option = this.filteredOptions[this.focusedOptionIndex];
          this.handleOptionSelect({ detail: option } as CustomEvent<AutocompleteMenuItem>);
        }
        break;
      case 'Escape':
        e.preventDefault();
        this.showDropdown = false;
        this.focusedOptionIndex = -1;
        break;
    }
  };

  private handleItemHover = (event: CustomEvent<number>) => {
    this.focusedOptionIndex = event.detail;
  };

  render() {
    const { tags, remaining } = this.visibleTags;

    return (
      <Host>
        <div
          class="input-autocomplete-wrapper"
          style={{ width: this.width }}
          onKeyDown={this.handleKeyDown}
        >
          {this.multiSelect ? (
            // Multi-select input with flowing tags
            <div class="input-wrapper">
              <bh-label
                label={this.showLabel ? this.label : ''}
                required={this.required}
                showHelpIcon={this.showHelpIcon}
                helpIconTooltip={this.helpIconTooltip}
                disabled={this.disabled}
                onBhHelpClick={() => this.bhHelpClick.emit()}
              >
              <div
                class={{
                  'input-field': true,
                  'autocomplete-multi-input': true,
                  'input-error': this.error,
                  'input-disabled': this.disabled,
                  'input-focused': this.isFocused,
                  'input-hover': this.isHovered,
                }}
                onMouseEnter={() => !this.isFocused && (this.isHovered = true)}
                onMouseLeave={() => (this.isHovered = false)}
              >
                <div class="input-content-flow">
                  {this.showLeadingIcon && (
                    <span class="leading-icon material-symbols-outlined">
                      {this.leadingIcon}
                    </span>
                  )}

                  {/* Tags */}
                  {tags.map(tag => (
                    <div
                      key={tag.id}
                      onClick={this.handleTagClick}
                      onMouseDown={e => e.preventDefault()}
                      style={{ cursor: 'pointer' }}
                    >
                      <bh-tag
                        variant="text-only"
                        size="sm"
                        label={tag.label}
                        dismissible={!this.disabled}
                        disabled={this.disabled}
                        maxWidth={150}
                        onBhDismiss={() => this.handleTagRemove(tag)}
                      ></bh-tag>
                    </div>
                  ))}

                  {remaining > 0 && (
                    <div
                      onClick={this.handleTagClick}
                      onMouseDown={e => e.preventDefault()}
                      style={{ cursor: 'pointer' }}
                    >
                      <bh-tag
                        variant="text-only"
                        size="sm"
                        label={`+${remaining}`}
                        dismissible={false}
                        disabled={this.disabled}
                      ></bh-tag>
                    </div>
                  )}

                  <input
                    type="text"
                    value={this.value}
                    placeholder={tags.length === 0 ? this.placeholder : ''}
                    disabled={this.disabled}
                    class="inline-input"
                    onInput={this.handleInputChange}
                    onFocus={this.handleInputFocus}
                    onBlur={this.handleInputBlur}
                  />

                  {this.shouldShowClearButton && (
                    <bh-button-icon
                      hierarchy="tertiary"
                      size="xs"
                      iconName="close"
                      onBhClick={this.handleClearAll}
                      disabled={this.disabled}
                    ></bh-button-icon>
                  )}
                </div>
              </div>
              </bh-label>

              {this.showHintText && this.hintText && (
                <div
                  class={{ 'hint-text': true, 'hint-error': this.error }}
                  style={{ opacity: this.showDropdown ? '0' : '1' }}
                >
                  {this.hintText}
                </div>
              )}
            </div>
          ) : (
            // Standard single-select using bh-input-text
            <bh-input-text
              state={this.getInputTextState()}
              error={this.error}
              required={this.required}
              disabled={this.disabled}
              showHelpIcon={this.showHelpIcon}
              showLeadingIcon={this.showLeadingIcon}
              showLabel={this.showLabel}
              showHintText={this.showHintText}
              label={this.label}
              placeholder={this.placeholder}
              hintText={this.hintText}
              leadingIcon={this.leadingIcon}
              helpIconTooltip={this.helpIconTooltip}
              value={this.value}
              width="100%"
              onBhInput={(e: CustomEvent<string>) => {
                this.value = e.detail;
                this.bhChange.emit(this.value);
                this.isTagClickOpen = false;
                if (this.value.length >= this.minSearchLength) {
                  this.showDropdown = true;
                } else {
                  this.showDropdown = false;
                }
              }}
              onBhFocus={this.handleInputFocus}
              onBhBlur={this.handleInputBlur}
              onBhHelpClick={() => this.bhHelpClick.emit()}
            ></bh-input-text>
          )}

          {/* Dropdown menu */}
          <bh-autocomplete-menu
            visible={this.showDropdown}
            menuItems={this.filteredOptions}
            selectedIndex={this.focusedOptionIndex}
            multiSelect={this.multiSelect}
            searchQuery={this.value}
            onBhItemClick={this.handleOptionSelect}
            onBhItemHover={this.handleItemHover}
          ></bh-autocomplete-menu>
        </div>
      </Host>
    );
  }
}
